header {
  background-color: #1a1a1a;
  border-bottom: 1px solid #333;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  display: flex;
  box-shadow: 0 4px 8px #0003;
}

header h1 {
  color: var(--title-color);
  text-shadow: 0 0 8px var(--title-color);
  margin: 0;
  font-size: 24px;
}

.main-tabs {
  background-color: var(--secondary-background);
  border-bottom: 1px solid var(--border-color);
  justify-content: space-around;
  padding: 10px;
  display: flex;
}

.main-tab {
  cursor: pointer;
  color: var(--dropdown-text-color);
  padding: 10px 20px;
  transition: color .3s, border-bottom .3s;
}

.main-tab:hover {
  color: #0c9;
}

.active-main-tab {
  border-bottom: 2px solid var(--dropdown-text-color);
  font-weight: bold;
}

.tabs {
  background-color: var(--secondary-background);
  border-bottom: 1px solid var(--border-color);
  justify-content: space-around;
  padding: 10px;
  display: flex;
}

.tab {
  cursor: pointer;
  color: var(--dropdown-text-color);
  border-radius: 5px;
  transition: color .3s, border-bottom .3s;
}

.tab:hover {
  color: #0c9;
}

.active-tab {
  border-bottom: 2px solid var(--dropdown-text-color);
  font-weight: bold;
}

.tab-content input[type="color"] {
  border-radius: 0;
  width: 20px;
  height: 20px;
}

.content {
  background-color: #1a1a1a;
  border-top: 1px solid #333;
  box-shadow: 0 -4px 8px #0003;
}

h2 {
  color: #0fc;
  text-shadow: 0 0 8px #0fc;
}

p {
  color: #ccc;
}

.hauling-missions {
  background-color: #1a1a1a;
  border: 1px solid #333;
  border-radius: 10px;
  flex-direction: column;
  gap: 10px;
  padding: 20px;
  display: flex;
  box-shadow: 0 4px 8px #0003;
}

.form-group {
  flex-direction: column;
  align-items: center;
  gap: 5px;
  display: flex;
}

.form-group label:not(.preferences label) {
  color: var(--dropdown-label-color);
  text-shadow: 0 0 8px var(--dropdown-label-color);
  text-align: center;
}

.form-group label[for="location-type"] {
  white-space: nowrap;
}

.form-row {
  align-items: center;
  gap: 10px;
  display: flex;
}

.button-group {
  background-color: #1a1a1a;
  border: 1px solid #333;
  border-radius: 10px;
  flex-flow: wrap;
  align-items: center;
  gap: 10px;
  width: 700px;
  margin-top: 20px;
  margin-left: 10px;
  padding: 10px;
  display: flex;
  box-shadow: 0 4px 8px #0003;
}

.button-group button {
  background-color: var(--button-color);
  color: #0d0d0d;
  cursor: pointer;
  white-space: nowrap;
  border: none;
  border-radius: 5px;
  padding: 7px 20px;
  font-size: 16px;
  transition: background-color .3s, color .3s;
}

.button-group button:hover {
  background-color: #0c9;
}

.clear-log-button {
  background-color: #f44 !important;
}

.clear-log-button:hover {
  background-color: #f66 !important;
}

.remove-cargo-button {
  background-color: #f44 !important;
}

.remove-cargo-button:hover {
  background-color: #f66 !important;
}

.add-entry-button, .table-view-button {
  flex-shrink: 0;
}

.actions {
  flex-wrap: nowrap;
  justify-content: flex-start;
  align-items: center;
  gap: 3px;
  min-width: max-content;
  display: flex;
}

.actions button {
  background-color: var(--button-color);
  color: #0d0d0d;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  height: 24px;
  padding: 3px 8px;
  font-size: 12px;
  transition: background-color .3s, color .3s;
}

.actions button:hover {
  background-color: #0c9;
}

.actions .form-group {
  flex-direction: row;
  align-items: center;
  gap: 5px;
}

.actions .form-group label {
  margin: 0;
}

.actions .form-group input {
  color: #0fc;
  background-color: #333;
  width: 60px;
  height: 24px;
  padding: 2px 4px;
}

.scu-container {
  text-align: center;
  flex-direction: column;
  align-items: center;
  gap: 5px;
  display: flex;
}

.scu-label {
  color: #0fc;
  white-space: pre-wrap;
  margin-left: 10px;
  font-size: 15px;
}

.scu-total {
  color: #0fc;
  font-size: 14px;
}

.amount-input {
  color: #0fc;
  background-color: #333;
  border: 1px solid #333;
  border-radius: 5px;
  width: 30px;
  height: 20px;
  padding: 10px;
}

.capture-key-settings {
  align-items: center;
  gap: 10px;
  margin: 10px 0;
  display: flex;
}

.capture-key-settings input {
  border: 1px solid var(--button-color);
  width: 100px;
  color: var(--dropdown-text-color);
  background: #333;
  border-radius: 3px;
  padding: 5px;
}

.capture-key-settings button {
  white-space: nowrap;
}

.selection-box {
  border: 2px solid var(--button-color);
  pointer-events: none;
  background-color: #0fc3;
  position: absolute;
}

.saved-selection-box {
  border: 2px dashed var(--button-color);
  pointer-events: none;
  background-color: #00ffcc1a;
  position: absolute;
}

.capture-table-group {
  border: 1px solid var(--table-outline-color);
  border-radius: 5px;
  margin-bottom: 20px;
  overflow: hidden;
}

.capture-tab-header {
  border: 1px solid var(--table-outline-color);
  background-color: #1a1a1a;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  display: flex;
}

.capture-tab-header h5 {
  color: var(--mission-text-color);
  margin: 0;
}

.reward-input-container {
  align-items: center;
  gap: 5px;
  display: flex;
}

.ocr-mission-payout-capture {
  width: 100px;
  color: var(--row-text-color);
  background-color: #333;
  border: 1px solid #444;
  border-radius: 4px;
  padding: 5px;
}

.date-group {
  border: 1px solid var(--table-outline-color);
  border-radius: 5px;
  margin-bottom: 30px;
  overflow: hidden;
}

.date-group h3 {
  color: var(--title-color);
  border-bottom: 1px solid var(--table-outline-color);
  background-color: #262626;
  margin: 0;
  padding: 15px;
}

.mission-entry {
  border: 1px solid var(--table-outline-color);
  background-color: #262626;
  border-radius: 5px;
  margin-bottom: 15px;
  overflow: hidden;
}

.mission-header {
  cursor: pointer;
  border-bottom: 1px solid var(--table-outline-color);
  background-color: #1a1a1a;
  justify-content: space-between;
  align-items: center;
  padding: 10px 15px;
  display: flex;
}

.mission-header h4 {
  color: var(--mission-text-color);
  margin: 0;
}

.mission-summary {
  color: var(--row-text-color);
  gap: 20px;
  display: flex;
}

.mission-details {
  padding: 15px;
}

.mission-details table {
  outline: 1px solid var(--table-outline-color);
  border-collapse: collapse;
  background-color: #262626;
  width: 100%;
  margin-bottom: 0;
}

.mission-details th {
  color: var(--table-header-text-color);
  text-align: left;
  border-bottom: 1px solid var(--table-outline-color);
  outline: 1px solid var(--table-outline-color);
  background-color: #1a1a1a;
  padding: 10px;
}

.mission-details td {
  border-bottom: 1px solid var(--table-outline-color);
  color: var(--row-text-color);
  outline: 1px solid var(--table-outline-color);
  padding: 8px 10px;
}

.mission-details tr:last-child td {
  border-bottom: none;
}

.date-total {
  color: var(--mission-text-color);
  text-align: right;
  border: 1px solid var(--table-outline-color);
  background-color: #262626;
  border-radius: 5px;
  margin-top: 15px;
  padding: 15px;
  font-weight: bold;
}

.scu-total-container {
  align-items: center;
  gap: 10px;
  margin: 10px 0;
  padding: 5px;
  display: flex;
}

.scu-total-container label {
  color: var(--dropdown-label-color);
  font-family: var(--main-font), sans-serif;
  font-size: 18px;
  font-weight: bold;
}

.scu-total-input {
  color: var(--dropdown-text-color);
  font-family: var(--main-font), sans-serif;
  text-align: right;
  background-color: #333;
  border: 1px solid #444;
  border-radius: 4px;
  width: 50px;
  padding: 5px 10px;
  font-size: 18px;
}

.hauling-mission-table th.pickup, .hauling-mission-table td.pickup {
  white-space: normal;
  width: auto;
  color: var(--row-text-color);
  overflow: visible;
}

.hauling-missions-tab {
  color: var(--text-color);
  padding: 20px;
  font-family: Orbitron, sans-serif;
}

.mission-entry-form {
  flex-wrap: wrap;
  gap: 10px;
  margin-bottom: 20px;
  display: flex;
}

.mission-entry-form > * {
  flex: auto;
  min-width: 120px;
}

.mission-entry-form label {
  color: var(--dropdown-label-color);
  margin-bottom: 5px;
  display: block;
}

.mission-entry-form input[type="text"], .mission-entry-form select {
  width: 100%;
  color: var(--dropdown-text-color);
  box-sizing: border-box;
  background-color: #333;
  border: 1px solid #333;
  border-radius: 4px;
  padding: 8px;
  font-family: Orbitron, sans-serif;
}

.mission-entry-form button {
  background-color: var(--button-color);
  color: #fff;
  cursor: pointer;
  border: none;
  border-radius: 4px;
  padding: 10px 15px;
  font-family: Orbitron, sans-serif;
  transition: background-color .3s;
}

.mission-entry-form button:hover {
  background-color: #00bfa5;
}

.manifest-table {
  border-collapse: collapse;
  width: 100%;
  margin-top: 20px;
}

.manifest-table th, .manifest-table td {
  border: 1px solid var(--table-outline-color);
  text-align: left;
  color: var(--row-text-color);
  padding: 8px;
}

.manifest-table th {
  color: var(--table-header-text-color);
  background-color: #333;
}

.manifest-table tr:nth-child(2n) {
  background-color: #444;
}

.manifest-table tr:hover {
  background-color: #555;
}

.drop-off-group {
  border: 1px solid #333;
  border-radius: 4px;
  margin-bottom: 20px;
}

.drop-off-header {
  color: var(--drop-off-header-text-color);
  cursor: pointer;
  background-color: #333;
  border-bottom: 1px solid #333;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
