}

.drop-off-header button:hover {
  background-color: #00bfa5;
}

.drop-off-content {
  padding: 10px;
}

.entry {
  border-bottom: 1px solid #333;
  justify-content: space-between;
  align-items: center;
  padding: 8px;
  display: flex;
}

.entry:last-child {
  border-bottom: none;
}

.entry span {
  flex: 1;
  margin-right: 10px;
}

.entry button {
  background-color: var(--button-color);
  color: #fff;
  cursor: pointer;
  border: none;
  border-radius: 4px;
  padding: 5px 10px;
  font-family: Orbitron, sans-serif;
  transition: background-color .3s;
}

.entry button:hover {
  background-color: #00bfa5;
}

.total-scu {
  color: var(--text-color);
  margin-top: 20px;
  font-size: 1.2em;
}

@media (width <= 768px) {
  .mission-entry-form {
    flex-direction: column;
  }

  .mission-entry-form > * {
    width: 100%;
  }

  .manifest-table th, .manifest-table td {
    box-sizing: border-box;
    width: 100%;
    display: block;
  }

  .manifest-table th {
    text-align: center;
  }

  .entry {
    flex-direction: column;
    align-items: flex-start;
  }

  .entry span {
    margin-bottom: 5px;
  }
}

